@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
  padding:0;
  margin:0;
}
body{
  min-height:100vh;
  /* background: rgb(238,245,219);
  background: linear-gradient(135deg, rgba(238,245,219,1) 0%, rgba(27,153,139,0.2) 100%); */
  color: #333745;
}
h1, h2, h3, h4, h5, li, button {
  font-family: 'Roboto';
}
p, input, textarea, select{
  font-family: 'Open Sans';
}
h1, h2, li {
  text-align: center;
}

h1{
  font-weight: 900;
}
nav{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  background: #333745;
  align-items: center;
  padding-left: 2%;
  margin-bottom: 3%;
  box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);;
}
.logo{
  color:#ff9100;
  font-size: 150%;
  letter-spacing: 5px;
  text-align: left;
  font-weight: 500;
  justify-self: center;
  line-height: 25px;
}
li{
  list-style-type: none;
}
a{
  text-decoration: none;
  color: #ff9100;
}
a:hover{
  text-decoration: underline;
}
nav a{
  text-transform: uppercase;
   text-align: center;
  box-sizing: border-box;
  height:100%;
  width:100%;
  padding:15% 0;
  margin:0;
  color:white;
}
nav a:hover{
  background-color: #4f5569;
}

.active{
  background-color: #4f5569;
}
button{
  letter-spacing: 0.03em;
  font-weight:500;
  border:none;
  border-radius: 5px;
  text-transform: uppercase;
  outline:none;
  box-shadow: none;
  background-color: #ff9100;
  color:white;
  margin:5% auto;
  padding: 16px;
    font-size: 0.875rem;
    min-width: 64px;
  outline:none;
  box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);;
  transition: all .3s;  
  max-width: 20vw;
  cursor: pointer;
}
button:hover, button:active{
  background-color: #333745;
  box-shadow: none;
}
button:disabled{
  opacity: .7;
}
.errorWrapper, .notificationWrapper{
  text-align: center;
  position: absolute;
  padding:5%;
  transform: translateY(-100vh);
  transition: .3s;
  background: white;
  top:10vh;
  z-index: 100;
  left:0;
  right:0;
  margin:auto;
  width: 50vw;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);;
}
.errorWrapper{
   background: #f44336;
  /* border:3px solid red; */
}
.notificationWrapper{
  background: #333745;
}
.show{
  transform: translateY(0);
}

.link{
  background: none;
  color:#ff9100;
  box-shadow: none;
  margin:0;
}
.link:hover{
  background:none;
  text-decoration: underline;
}

.notAuthorized{
  text-align: center;
}
.notAuthorized a{
  color: #333745;
  text-decoration: underline;
}

main{
  padding:0 5%;
}
.searchForm{
  background:none;
  box-shadow: none;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap:1.5em;
  padding:0;
}
.searchForm button{
  margin: 22px 0 8px 0;
  width: 100%;
}
.btn{
  color:#ff9100;
}
.dltBtn{
  color: #333745;
}
.dltBtn, .btn{
  display: grid;
  grid-template-columns: .1fr 1fr;
  align-content: center;
  max-width: 20vw;
  line-height: 15px;
  float:right;
  transition: all .3s;
  cursor: pointer;
}
.btn:hover{
  text-decoration: underline;

}
form{
    display:grid;
    grid-template-columns: 1fr;
    margin: 0 auto;
    width:50vw;
    box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);;
    border-radius: 5px;
    padding:5%;
}
form h3{
    text-align: center;
}
form h1{
    margin:1%;
}

.loginPage a{
    text-align: center;
    display: block;
    margin:1% 0 5%;
}
textarea{
    width:100%;
    height:100px;
}



.profile{
    margin:0 3em 3em;
    display: grid;
}
.profile h2{
 margin: 5% 0 2% 0;
}
.profile .review{
    margin-top: 2%;
}
.profile .btn{
    justify-self: right;
}
.library{
    margin:2% 0;
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-gap:2rem;
}
.book{
    /* background: #EEF5DB; */
    width: 100%;
    height:100%;
    box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);;
    padding:5%;
    border-radius:5px;
    justify-content: center;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: .5fr 1fr .5fr ;
    grid-gap:3%;
    position: relative;
}
.editBtn{
    display:grid;
    margin: 2% 0;
    justify-self: right;
    justify-content: right;
    justify-items: right;
    /* float:right; */
}

.book img{
    grid-row:1/4;
    justify-self: center;
}
.book button{
    margin:0;
    box-shadow: none;
}

.book a, .review a{
    color:#ff9100;
    text-decoration: underline;
    text-align: right;
}
.shown{
    box-shadow: none;
    background-color:#333745;
}

.editForm{
    max-height: 0;
    padding:0;
    overflow: hidden;
    transition: all .3s;
    justify-self: right;
    margin:0 0 3%;
}
.showForm{
    padding:3% 10%;
    max-height: 100vw;
}
.model{
    position: fixed;
    background: rgba(0,0,0,.4);
    width:100vw;
    height:100vh;
    top:0;
    left:0;
    display: grid;
}
.model-content{
    /* height:40vh; */
    top:10vh;
    width:70vw;
    margin:auto;
    background:white;
    padding:5%;
    text-align: center;
    border-radius: 5px;
    box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);;
    align-self: center;
    overflow: scroll;
}
.model h1, .model p{
    padding:1% 0;
}
.model-content button{
    margin: 5% 3%;
}
.modelDltBtn{
    background-color:rgb(61, 60, 60);
}
.searchResultContainer{
    width:70vw;
    margin:0 auto 25vw;
    display: grid;
    grid-gap:1%;
    justify-items: center;
}
.searchResult{
    /* background: #EEF5DB; */
    width: 100%;
    box-shadow: 0px 4px 10px rgba(0,0,0,.4);
    padding:3%;
    justify-content: center;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: .1fr .1fr 1fr .1fr ;
    grid-gap:1%;
    position: relative;
    border-radius:5px;
}
.searchResult img{
    grid-row:1/6;
    justify-self: center;
    height:200px;
    width:auto;
    justify-self: center;
    align-self: center;
}
.searchResult>div{
    align-items: end;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.searchResult>div p:last-child{
    text-align: right;
    grid-column: 2;
}
.searchResult>div p:first-child{
    font-weight: bold;
}

h1{
    margin:3%;
}

.heartIcon{
    position: absolute;
    right:3%;
    top:3%;
    fill: #ff9100;
    float: right;
    cursor: pointer;
}
p>span{
    color:#ff9100;
    cursor: pointer;
    font-size:150%;
}
.reviewForm{
    display: grid;
    grid-template-columns: .5fr 1fr;
    margin:5%;
}


h1{
    grid-column: 1/-1;
}
img{
    width: 100%;
}
.review{
    display: grid;
    grid-template-columns: .5fr 1fr;
    /* grid-template-rows: .1fr .1fr .1fr 1fr ; */
    grid-gap: 1em;
    margin:5% auto;
    padding:5%;
    box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);;
    padding:3%;
    border-radius: 5px;
    width:70vw;
}
.link-editBtn{
    grid-column: 1/-1;
    display: inline;

}.review .btn{
    justify-self: right;
}
.review img{
    grid-row:2/5;
    width:auto;
    height:300px;
    justify-self: center;
}
.rating{
    text-align: right;
}
.review .pub-authors {
    grid-template-columns: 2fr 1fr;
}

.reviewsWrapper .clearBtn{
    float: none;
    grid-template-columns: 1fr;
    /* text-align: center; */
   
    margin:0 19vw;
}
.bookDetail{
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: .1fr .1fr 1fr .1FR;
    grid-gap: 1em 2em;
    padding:0 5% 5%;
}
/* .bookDetail h1, .bookDetail h4,.bookDetailw p{
    grid-column: 2;
} */
.bookDetail a{
    text-align: right;
}
.bookDetail img{
    grid-row:1/6;
    grid-column:1;
    justify-self: center;
    /* align-self: center; */
}
.pub-authors{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.pub-authors div:last-child{
    text-align: right;
}

